import { _decorator, Component, Node, Label } from 'cc';
import { EventType } from '../../../EventType';
import { observer } from '../../../game/App';
const { ccclass, property } = _decorator;

@ccclass('TabViewListItemComponent')
export class TabViewListItemComponent extends Component {
    _data: any;
    @property(Label)
    public Label_name: Label = null;
    start() {
        this.node.on("setdata",this.setData,this);
        this.node.on(Node.EventType.TOUCH_END, this.touchEndHandler, this);

    }
    private touchEndHandler(evt){
    }

    public setData(data: any) {
        this._data = data;
        this.Label_name.string = this._data.name;
    }
    update(deltaTime: number) {
        
    }
    onDestroy(){
        this.node.off("setdata");
    }
}

